
.detail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  min-height: 0;
  max-height: 100vh;
  font-size: .9rem;
}
.detail .b-tabs {
    width: 100%;
}
.detail .break-word {
    word-break: break-word;
}
.detail .overflow-container {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow-y: auto;
}
.detail .panel {
    margin: .75rem;
}
.detail .task-text {
    max-width: 90%;
    display: block;
}
.detail .task-description {
    margin-bottom: 1em;
}
.detail .task-description h1, .detail .task-description h2, .detail .task-description h3, .detail .task-description h4, .detail .task-description h5, .detail .task-description ul {
      margin: .2em 0;
}
.detail .task-description h1 {
      font-size: 1.5em;
}
.detail .task-description h2 {
      font-size: 1.35em;
}
.detail .task-description h3 {
      font-size: 1.2em;
}
.detail .task-description h4 {
      font-size: 1.05em;
}
.detail .task-description h5 {
      font-size: 1em;
}
.detail .task-description ul {
      margin-left: 1.2em;
      list-style: disc;
}
.detail .task-description ul li input[type=checkbox] {
        margin-right: 1em;
        margin-left: -1.5em;
}
.detail .imdone-tags {
    padding: 8px 8px 0px 8px;
}



