
img.gravatar {
  border-radius: 50%;
  width: 25px;
}
.task-card {
  background: inherit;
  position: relative;
  cursor: -webkit-grab;
}
.task-card:not(:last-child) {
    margin-bottom: .75em;
}
.task-card .toggle {
    background: inherit;
    opacity: 0.8;
}
.task-card .toggle-parent {
    background: inherit;
}
.task-card .card-actions {
    position: absolute;
    top: 2px;
    left: 0;
    width: 100%;
}
.task-card .card-actions a {
      text-decoration: none;
      margin-right: 5px;
}
.task-card .message-body {
    background: inherit;
    word-break: break-word;
    text-align: left;
    padding: 1em;
    font-size: .9rem !important;
}
.task-card .message-body .tags {
      margin-bottom: 0;
      padding-bottom: 0;
}
.task-card .toggle-full-desc {
    margin-bottom: 1em;
}
.task-card .source {
    position: absolute;
    bottom: 0;
    left: 1em;
}
.task-card .task-text h1, .task-card .task-text h2, .task-card .task-text h3, .task-card .task-text h4, .task-card .task-text h5, .task-card .task-text ul {
    margin: .2em 0;
}
.task-card .task-text h1 {
    font-size: 1.5em;
}
.task-card .task-text h2 {
    font-size: 1.35em;
}
.task-card .task-text h3 {
    font-size: 1.2em;
}
.task-card .task-text h4 {
    font-size: 1.05em;
}
.task-card .task-text h5 {
    font-size: 1em;
}
.task-card .task-text ol {
    margin-left: 1em;
}
.task-card .task-text ul {
    margin-left: 1.2em;
    list-style: disc;
}
.task-card .task-text ul li input[type=checkbox] {
      margin-right: 1em;
      margin-left: -1.5em;
}



